.btn {
  display: inline-block;
  font-family: $buttonFont;
  font-size: 30px;
  line-height: 1.2;
  font-weight: 700;
  color: #000;
  vertical-align: middle;
  text-align: center;
  text-decoration: none;
  white-space: nowrap;
  background-color: transparent;
  border: none;
  outline: none;
  transition: all .3s;
  user-select: none;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  &:hover {
    cursor: pointer;
  }
  &--menu {
    position: relative;
    z-index: 101;
    width: 47px;
    height: 47px;
    border: 2px solid #00a651;
    &:hover {
      cursor: pointer;
      background-color: $mainColor;
      & > span {
        &::after {
          transform: translateY(-1.5px) rotate(4.5deg);
        }
        &::before {
          transform: translateY(1.5px) rotate(-4.5deg);
          
        }
      }
    }
    &.is-open {
      & > span {
        background-color: transparent;
        &::after {
          transform: translateY(-9px) rotate(45deg);
        }
        &::before {
          transform: translateY(9px) rotate(-45deg);
        }
      }
      &:hover {
        & > span {
          background-color: rgba(#fff, 0.1);
          &::after {
            transform: translateY(-8px) rotate(40.5deg);
          }
          &::before {
            transform: translateY(8px) rotate(-40.5deg);
          }
        }
      }
    }//is-open
    &.is-dark {
      border-color: transparent;
      & > span {
        &::before, &::after {
          background-color: #000;
        }
      }
      &:hover {
        background-color: transparent;
        & > span {
          background-color: rgba(#000, 0.1);
        }
      }
    }
    & > span {
      position: absolute;
      top: 50%;
      height: 2px;
      left: 7px;
      right: 7px;
      margin-top: -1px;
      background-color: #fff;
      transition: background-color .3s;
      backface-visibility: hidden;
      &::before, &::after {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        height: 2px;
        background-color: #fff;
        transition: background-color .3s, transform 0.3s;
        backface-visibility: hidden;
      }
      &::before {
        top: -9px;
      }
      &::after {
        bottom: -9px;
      }
    }
  }//btn--menu
  &--std {
    position: relative;
    padding: 10px 30px 15px;
    color: #fff;
    font-style: italic;
    background-color: #00a651;
    border-radius: 10px;
    box-shadow: 5.7px 32.5px 250px #fff;
    &::after {
      content: '';
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      border-radius: 10px;
      box-shadow: 5.7px 32.5px 250px #fff;
      opacity: 0;
      transition: opacity .3s;
    }
    &:hover {
      &::after {
       opacity: 1; 
      } 
    }
  }
}//btn