.b-field {
	$p: &;
	position: relative;
	font-size: 25px;
	input:not([type="submit"]), textarea, select {
		padding: 18px 20px 17px;
		background-color: #f5f5f5;
		border: 1px solid #c0c0c0;
		outline: none;
		transition: all 0.4s;
		font-family: $mainFont;
		line-height: 1.2;
		@include placeholder {
			line-height: 1.2;
			opacity: 1;
			color: #969696;
			transition: opacity 0.5s;
			font-family: $mainFont;
		}
		&:focus {
			@include placeholder {
				opacity: 0;
			}
		}
	}
	textarea {
		resize: none;
		height: 100px;
	}
	&.has-error {
		.b-input input:not([type="submit"]) {
			box-shadow: 0 0 8px 2px red;
		}
		#{$p}__error {
			opacity: 1;
			z-index: 1;
			transform: scale(1);
			transition: opacity 0.5s ease, transform 0.5s ease, z-index 0s;
		}
	}
	&__error {
		position: absolute;
		left: 10%;
		top: 115%;
		opacity: 0;
		transition: opacity 0.5s ease, transform 0.5s ease, z-index 1s;
		background-color:#fff;
		box-shadow: 0 0 2px rgba(#000, 0.5);
		padding: 5px 15px;
		z-index: -1;
		font-size: 0.7em;
		transform: scale(0.9);
		white-space: nowrap;
		//color: #f2736d;
		text-align: left;
		user-select: none;
		cursor: default;
	}
}//b-field

.b-input {
	& > input:not([type="submit"]) {
		display: block;
		width: 100%;
		padding: 12px 20px;
		border-radius: 10px;
	}
}