.b-preloader {
  $p: &;
  position: fixed;
  top: 0; left: 0; bottom: 0; right: 0;
  background-color: #000;
  z-index: 10000;
  transform: scaleY(0);
  transform-origin: center top;
  visibility: hidden;
  transition: all 1s;
  &.is-show {
    transform: scaleY(1);
    visibility: visible;
    #{$p}__inner {
      opacity: 1;
    }
  }
  &__inner {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10001;
    opacity: 0;
    transition: opacity 0.2s;
    p {
      font-family: sans-serif;
      margin-top: 10px;
      color: $mainColor;
      text-align: center;
    }
  }

  .cssload-loader {
    position: relative;
    left: 50%;
    width: 62px;
    height: 62px;
    margin-left: -31px;
    border-radius: 50%;
    perspective: 780px;
  }

  .cssload-inner {
    position: absolute;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    border-radius: 50%;
  }

  .cssload-inner.cssload-one {
    left: 0%;
    top: 0%;
    animation: cssload-rotate-one 1.15s linear infinite;
    border-bottom: 3px solid $mainColor;
    @at-root {
      @keyframes cssload-rotate-one {
        0% {
          transform: rotateX(35deg) rotateY(-45deg) rotateZ(0deg);
        }
        100% {
          transform: rotateX(35deg) rotateY(-45deg) rotateZ(360deg);
        }
      }
    }
  }

  .cssload-inner.cssload-two {
    right: 0%;
    top: 0%;
    animation: cssload-rotate-two 1.15s linear infinite;
    border-right: 3px solid $mainColor;
    @at-root {
      @keyframes cssload-rotate-two {
        0% {
          transform: rotateX(50deg) rotateY(10deg) rotateZ(0deg);
        }
        100% {
          transform: rotateX(50deg) rotateY(10deg) rotateZ(360deg);
        }
      }
    }
  }

  .cssload-inner.cssload-three {
    right: 0%;
    bottom: 0%;
    animation: cssload-rotate-three 1.15s linear infinite;
    border-top: 3px solid $mainColor;
    @at-root {
      @keyframes cssload-rotate-three {
        0% {
          transform: rotateX(35deg) rotateY(55deg) rotateZ(0deg);
        }
        100% {
          transform: rotateX(35deg) rotateY(55deg) rotateZ(360deg);
        }
      }
    }
  }
}
  
.b-header {
  position: relative;
  z-index: 1;
  height: 100vh;
  min-height: 600px;
  max-height: 1020px;
  padding: 40px 35px 20px;
  overflow: hidden;
  background: url($img + 'header-bg.jpg') no-repeat center top;
  background-size: cover;
  @include r(768) {
    background-image: url($img + 'header-bg-768.jpg');
  }
  @include rh(700) {
    height: auto;
  }
  @include r(959) {
    padding: 30px 15px 20px;
    height: auto;
  }
  @include r(550) {
    padding: 15px 15px 20px;
    max-height: none;
  }
  &::before {
    content: '';
    position: absolute;
    left: 0; top: 0; right: 0; bottom: 0;
    z-index: -1;
    background-color: rgba(#000, 0.7);
  }
  &__top {
    margin-bottom: 30px;
    @include r(550) {
      display: block;
    }
  }
  &__left {
    order: 1;
    cursor: default;
    @include r(959) {
      text-align: center;
    }
    .b-logo, .b-social {
      display: inline-block;
      vertical-align: middle;
    }
    .b-logo {
      min-width: 170px;
      transform: translateY(5px);
      @include r(550) {
        display: none;
      }
    }
    .b-social {
      position: relative;
      padding-left: 15px;
      margin-left: 10px;
      @include r(959) {
        display: block;
        padding-left: 0;
        margin: 15px auto 0;
      }
      @include r(767) {
        display: none;
      }
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        height: 70px;
        width: 1px;
        margin-top: -35px;
        background-color: $mainColor;
        @include r(959) {
          content: none;
        }
      }
    }
  }//b-header__left
  &__right {
    order: 2;
    min-width: 470px;
    font-size: 0;
    @include r(767) {
      min-width: 0;
      //text-align: center;
    }
    @include r(550) {
      margin-bottom: 10px;
      text-align: right;
    }
    .b-contacts, .btn--menu {
      display: inline-block;
      vertical-align: middle;
    }
    .b-contacts {
      margin-right: 25px;
      @include r(767) {
        margin-right: 15px;
        //font-size: 16px;
        .b-contacts__item {
          margin-bottom: 0;
          a {
            display: table;
            &::after {
              content: none;
            }
          }
          .svg {
            display: none;
          }
          &:last-of-type {
            display: none;
          }
        }
        //text-align: center;
      }
    }
  }//b-header__right
  &__middle {
    display: flex;
    height: calc(100vh - 150px);
    min-height: 450px;
    max-height: 870px;
    width: 100%;
    padding-bottom: 40px;
    flex-direction: column;
    justify-content: space-around;
    @include rh(700) {
      height: auto;
    }
    @include r(767) {
      height: auto;
      max-height: none;
      display: block;
      padding-bottom: 0;
    }
  }
}//b-header

.b-menu {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 100;
  display: flex;
  align-items: flex-start;
  justify-content: space-around;
  flex-direction: column;
  max-width: 540px;
  min-height: 500px;
  padding: 50px;
  font-size: 50px;
  line-height: 1;
  text-align: left;
  background-color: #fff;
  transform: translateX(100%);
  transition: transform .5s;
  @include rh(500) {
    min-height: 0;
    padding-top: 20px;
    padding-bottom: 20px;
  }
  @include r(767) {
    font-size: 30px;
    position: fixed;
    bottom: auto;
  }
  @include r(479) {
    padding: 50px 20px;
  }
  &.is-open {
    transform: translateX(0);
    transition: transform .7s;
  }
  &__link {
    position: relative;
    display: block;
    margin-bottom: 10px;
    color: #000;
    font-family: MinionPro, serif;
    text-decoration: none;
    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      height: 2px;
      left: 0;
      right: 0;
      background-color: $mainColor;
      transform: scaleX(0);
      transform-origin: left center;
      transition: transform .3s;
    }
    &:hover {
      &::after {
        transform: scaleX(1);
      }
    }
  }
}//b-menu

.b-intro {
  position: relative;
  margin-bottom: 20px;
  padding: 0 60px;
