.b-mouse {
  display: table;
  margin: 0 auto;
  align-self: center;
  @include r(959) {
    display: none;
  }
  & > .svg {
    color: #b1b0b1;
    animation: mouse-move 2s linear infinite;
    @at-root {
      @keyframes mouse-move {
        0% {
          opacity: 0;
          transform: translateY(0)
        }
        30% {
          opacity: 1;
          transform: translateY(30%)
        }
        70% {
          opacity: 1;
          transform: translateY(70%)
        }
        100% {
          opacity: 0;
          transform: translateY(100%)
        }
      }
    }
  }
}//b-mouse

