.b-about {
  $p: &;
  padding-top: 30px;
  padding-bottom: 50px;
  @include r(767) {
    padding-bottom: 30px;
  }
  &__item {
    position: relative;
    display: inline-block;
    margin: 0.5%;
    border: 15px solid #fff;
    box-shadow: 0 0 40px rgba(0, 0, 0, 0.35);
    user-select: none;
    @include r(767) {
      border-width: 5px;
    }
    @include r(550) {
      display: block;
      width: 100% !important;
      margin-bottom: 10px;
    }
    &:nth-of-type(1) {
      width: 47%;
    }
    &:nth-of-type(2) {
      width: 51%;
    }
    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
  }
  &__row {
    max-width: 1370px;
    font-size: 0;
    &:last-of-type {
      margin-left: auto;
      #{$p}__item {
        vertical-align: top;
        &:nth-of-type(1) {
          width: 51%;
        }
        &:nth-of-type(2) {
          width: 47%;
        }
      }
    }
  }
  &__text {
    position: absolute;
    bottom: 0;
    right: 0;
    left: 0;
    padding: 3px 5px;
    color: #fff;
    font-size: 20px;
    text-align: center;
    background-image: linear-gradient(to top, #000 0%, rgba(0, 0, 0, 0) 100%);
    @include r(767) {
      font-size: 16px;
    }
  }
}//b-about

.b-prices {
  margin-bottom: 40px;
  .b-title {
    margin-bottom: 20px;
  }
}//b-prices

.b-tabs {
  &__title {
    &.type-main {
      a {
        width: 50%;
        padding: 15px;
        color: #000;
        font-size: 30px;
        text-align: center;
        text-decoration: none;
        background-color: #f1f1f1;
        transition: all 0.1s;
        touch-action: manipulation;
        @include r(767) {
          font-size: 24px;
        }
        @include r(479) {
          font-size: 20px;
          padding: 15px 5px;
        }
        &:hover, &.is-active {
          color: #fff;
          background-color: #363636;
        }
        &.is-active {
          pointer-events: none;
          cursor: default;
        }
      }
    }
    &.type-secondary {
      margin-bottom: 10px;
      border-bottom: 2px solid #e1e1e1;
      @include r(1000) {
        display: block;
        text-align: center;
      }
      &.type-male {
        justify-content: flex-start;
        a {
          margin-right: 30px;
        }
      }
      a {
        position: relative;
        display: inline-block;
        padding: 30px 0;
        color: #000;
        font-size: 25px;
        text-decoration: none;
        transition: all 0.3s;
        touch-action: manipulation;
        @include r(1350) {
          margin-right: 10px;
          text-align: center;
          &:last-of-type {
            margin-right: 0;
          }
        }
        @include r(1000) {
          padding: 5px 0;
        }
        @include r(767) {
          font-size: 16px;
        }
        @include r(374) {
          font-size: 15px;
        }
        &::after {
          content: '';
          position: absolute;
          left: 0;
          right: 0;
          bottom: -2px;
          height: 2px;
          background-color: #29aa50;
          opacity: 0;
          transition: opacity 0.3s;
        }
        &:hover, &.is-active {
          color: #29aa50;
        }
        &.is-active {
          pointer-events: none;
          cursor: default;
          &::after {
            opacity: 1;
          }
        }
      }
    }
  }
}//b-tabs

.b-collapse {
  &-item {
    $p: &;
    &:nth-of-type(odd) {
      #{$p}__title {
        background-color: #f1f1f1;
      }
      tr:nth-of-type(even) td {
        background-color: #f1f1f1;
      }
    }
    &:nth-of-type(even) {
      tr:nth-of-type(odd) td {
        background-color: #f1f1f1;
      }
    }
    &.is-open {
      #{$p}__content {
        display: table;
      }
      #{$p}__title {
        &::after {
          content: '×';
        }
      }
    }
    &__title {
      padding: 15px;
      color: #000;
      font-size: 30px;
      font-weight: 400;
      text-align: center;
      user-select: none;
      touch-action: manipulation;
      @include r(767) {
        padding: 5px 10px;
        font-size: 20px;
      }
      &:hover {
        cursor: pointer;
      }
      &::after {
        content: '+';
        padding-left: 10px;
      }
    }
    &__content {
      display: none;
      width: 100%;
      color: #000;
      font-size: 25px;
      border-collapse: collapse;
      @include r(767) {
        font-size: 16px;
      }
      td {
        padding: 15px 30px;
        @include r(767) {
          padding: 5px 10px;
        }
      }
      td:last-of-type {
        color: #0d0d0d;
        text-align: right;
        .special {
          position: relative;
          color: #ff3939;
          span {
            position: absolute;
            right: -15px;
            top: -10px;
            color: #000;
            font-size: 16px;
          }
        }
      }
    }//b-collapse-item__content
  }
}//b-collapse

