    }
  }
  &__list {
    height: 750px;
    padding: 100px 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    @include r(1450) {
      height: 550px;
      padding: 80px 0;
    }
    @include r(1000) {
