.b-gallery {
  margin-bottom: 30px;
  padding-top: 25px;
  @include clearfix;
  @include r(767) {
    margin-bottom: 10px;
  }
  .b-catalog {
    float: left;
    width: 570px;
    @include r(1450) {
      width: 400px;
    }
    @include r(1000) {
      float: none;
      width: auto;
    }
  }
  &__right {
    padding-top: 50px;
    margin-left: 740px;
    @include r(1450) {
      margin-left: 500px;
    }
    @include r(1000) {
      padding-top: 20px;
      margin-left: 0;
    }
  }
  &__title {
    position: relative;
    margin-bottom: 40px;
    color: #000;
    font-size: 50px;
    font-weight: 40;
    @include r(1000) {
      margin-bottom: 20px;
    }
    @include r(767) {
      display: none;
    }
    &::before {
      content: '';
      position: absolute;
      left: -80px;
      top: 27px;
      width: 50px;
      height: 7px;
      background-color: $mainColor;
    }
    span {
      font-weight: 700;
    }
  }
  &__text {
    margin-bottom: 75px;
    color: #707070;
    font-size: 25px;
    @include r(1000) {
      margin-bottom: 30px;
    }
    @include r(767) {
      display: none;
    }
  }
  &__slider {
    position: relative;
    padding-bottom: 100px;
    overflow: hidden;
    @include r(1200) {
      padding-bottom: 50px;
    }
    @include r(767) {
      padding-bottom: 30px;
    }
    .swiper-slide {
      width: auto;
    }
    &-wrap {
      position: relative;
      display: none;
      &.is-active {
        display: block;
      }
    }
  }
  &__item {
    display: block;
    position: relative;
    &:hover {
      &::after {
        opacity: 1;
      }
    }
    &::after {
      content: '';
      position: absolute;
      z-index: 1;
      top: 0; right: 0; bottom: 0; left: 0;
      background: rgba(#000, 0.78) url($img + 'icons/zoom.svg') no-repeat center center;
      background-size: 75px 75px;
      opacity: 0;
      transition: opacity 0.3s;
    }
    img {
      display: block;
      @include r(1600) {
        max-width: 250px;
      }
      @include r(1450) {
        max-width: 200px;
        height: auto;
      }
    }
  }
}//b-gallery

.b-team {
  padding-top: 40px;
  padding-bottom: 80px;
  &__slider {
    position: relative;
    overflow: hidden;
    .swiper-wrapper {
      //align-items: center;
      .swiper-slide-active {
        .b-master::before {
          opacity: 1;
        }
      }
    }
    .swiper-slide {
      width: auto;
      transition: all 0.7s;
      @include r(767) {
        width: 100%;
      }
    }
    .b-slider__prev {
      right: calc(50% + 500px);
      @include r(1200) {
        right: auto;
        left: 10px;
      }
    }
    .b-slider__next {
      left: calc(50% + 500px);
      @include r(1200) {
        left: auto;
        right: 10px;
      }
    }
  }
}//b-team

.b-master {
