    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    background: url($img + 'icons/border.svg') no-repeat center center;
    background-size: cover;
    opacity: 0;
    transition: opacity 0.4s;
  }
  img {
    display: block;
    margin: 0 auto;
