}//b-master;

.b-video-responces {
  $p: &;
  padding-top: 70px;
  padding-bottom: 20px;
  @include r(767) {
    padding-top: 30px;
    padding-bottom: 10px;
  }
  &__list {
    align-items: stretch;
    @include r(767) {
      display: block;
    }
  }
  &__vertical {
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    justify-content: space-between;
    width: 300px;
    margin-left: 40px;
    @include r(1350) {
      margin-left: 10px;
    }
    @include r(1000) {
      width: 200px;
    }
    @include r(767) {
      flex-direction: row;
      margin-left: 0;
      width: auto;
      #{$p}__item {
        margin-right: 5px;
        &:last-of-type {
          margin-right: 0;
        }
      }
    }
    .b-video {
      & > .svg {
        margin-top: -8px;
        margin-left: -4px;
      }
    }
  }
  &__item {
    position: relative;
    display: block;
    //max-width: 900px;
    width: 100%;
    margin: 0 auto 5px;
    background-position: center center;
    background-size: cover;
    border: 15px solid #fff;
    box-shadow: 0 0 21px rgba(13, 13, 13, 0.47);
    &::before {
      content: '';
      display: inline-block;
      padding-bottom: 56.25%;
    }
    @include r(1350) {
      border-width: 5px;
    }
    @include r(767) {
      max-width: 500px;
      margin: 0 auto 5px;
    }
    &:hover {
      .b-video {
        opacity: 1;
        & > .svg {
          color: transparent;
        }
      }
      &::after {
        opacity: 1;
      }
    }
    &::after {
      content: '';
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      background-color: rgba(#000, 0.72);
      opacity: 0;
      transition: all .3s;
    }
    @include rm(768) {
      &.is-main {
        &:hover {
          .b-video {
            opacity: 1;
            & > .svg {
              color: #fff;
            }
          }
        }
        &::after {
          opacity: 1;
        }
        .b-video {
          opacity: 1;
        }
      }
    }
    &.hide-bg {
      &::after {
        opacity: 0;
        transition: opacity 1s;
      }
      .b-video {
        opacity: 0 !important;
        transition: opacity 1s;
      }
    }
    iframe {
      position: absolute;
      top: 0; right: 0; bottom: 0; left: 0;
      z-index: 5;
      width: 100%;
      height: 100%;
      border: none;
    }
    .b-video {
      position: absolute;
      top: 50%;
      left: 50%;
      z-index: 1;
      transform: translate(-50%, -50%);
      opacity: 0;
      transition: opacity .3s;
      @include r(767) {
        opacity: 1;
      }
      @include r(479) {
        font-size: 8px;
        & > .svg {
          width: 14px;
          height: 20px;
          margin-top: -6px;
          margin-left: -2px;
        }
      }
    }
    img {
      // display: block;
      // max-width: 100%;
      // height: auto;
    }
  }
}

