.b-footer {
  padding: 35px;
  background: url($img + 'footer-bg.png') no-repeat center center;
  background-size: cover;
  @include r(768) {
    background: #1f1f1d;
  }
  @include r(550) {
    display: block;
    height: auto;
  }
  @include r(479) {
    padding: 20px 10px;
  }
  &__left {
    order: 1;
    min-width: 320px;
    @include r(767) {
      min-width: 0;
      text-align: center;
    }
    cursor: default;
    .b-logo, .b-social {
      display: inline-block;
      vertical-align: middle;
    }
    .b-logo {
      transform: translateY(5px);
    }
    .b-social {
      position: relative;
      padding-left: 15px;
      margin-left: 10px;
      @include r(767) {
        padding-left: 0;
        margin: 15px 10px 0;
      }
      &::before {
        content: '';
        position: absolute;
        left: 0;
        top: 50%;
        height: 70px;
        width: 1px;
        margin-top: -35px;
        background-color: $mainColor;
        @include r(767) {
          content: none;
        }
      }
    }
  }
  &__right {
    order: 2;
    font-size: 0;
    .b-contacts, .btn--menu {
      display: inline-block;
      vertical-align: middle;
    }
    .b-contacts {
      margin-right: 25px;
      @include r(550) {
        display: block;
        margin: 0 auto 15px;
        text-align: center;
      }
    }
  }
}//b-footer