@import "../components/layout";
@import "../components/button";
@import "../components/input";

.b-flex {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.b-logo {
  display: inline-block;
  vertical-align: middle;
  outline: none;
  img {
    display: block;
    max-width: 100%;
    height: auto;
  }
}//b-logo

.b-title {
  margin: 0 auto 70px;
  color: #000;
  font-size: 40px;
  font-weight: 400;
  text-align: center;
  @include r(1023) {
    font-size: 30px;
    br {
      display: none;
    }
  }
  @include r(767) {
    margin-bottom: 30px;
  }
  @include r(479) {
    font-size: 24px;
  }
}//b-title

.b-social {
  &__title {
    margin-bottom: 2px;
    color: #fff;
    font-size: 15px;
    user-select: none;
  }
  &__list {
    padding-left: 5px;
    font-size: 0;
  }
  &__item {
    position: relative;
    display: inline-block;
    width: 25px;
    height: 25px;
    font-size: 20px;
    color: #fff;
    vertical-align: middle;
    text-align: center;
    transition: color 0.3s;
    overflow: hidden;
    &:hover {
      color: $mainColor;
    }
    .svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}//b-social

.b-contacts {
  color: #fff;
  font-weight: 700;
  font-size: 21px;
  text-align: right;
  cursor: default;
  &__item {
    margin-bottom: 10px;
    &:last-of-type {
      margin-bottom: 0;
    }
    a {
      position: relative;
      margin-right: 18px;
      color: inherit;
      font-size: 0.9523em;
      text-decoration: none;
      white-space: nowrap;
      transition: color 0.3s;
      &:hover {
        color: $mainColor;
      }
      &::after {
        content: '/';
        // margin: 0 3px;
        color: $mainColor;
        pointer-events: none;
        position: absolute;
        right: -14px;
        top: 50%;
        transform: translateY(-50%);
      }
      &:last-of-type {
        margin-right: 0;
        &::after {
          content: none;
        }
      }
    }
    .svg {
      color: $mainColor;
    }
  }//b-contacts__item
}//b-contacts

.b-video {
  display: block;
  position: relative;
  font-size: 12px;
  width: .875em;
  height: 1.125em;
  &:hover {
    & > .svg {
      color: rgb(255, 241, 14);
    }
  }
  & > .svg {
    display: block;
    margin-top: -1px;
    font-size: 1.4em;
    color: transparent;
    transition: color .3s;    
  }
  &__circle {
    display: block;
    position: absolute;
    top: -1.9375em;
    left: -2.125em;
    width: 4.8125em;
    height: 4.8125em;
    border-radius: 100%;
    border: 0.125em solid rgba(255,255,255,.4);
    border-left-color: rgba(255,255,255,.1);
    border-top-color: rgba(255,255,255,.1);
    transform: rotate(-45deg);
    animation: loaderleft 10s linear infinite;
    @include r(767) {
      animation: none;
    }
    @at-root {
      @keyframes loaderleft {
        0% { transform: rotate(360deg) }
        50% { transform: rotate(180deg) }
        100% { transform: rotate(0) }
      }
    }
  }
  &__circle-inner {
    display: block;
    position: absolute;
    right: -3.3125em;
    top: -3.5em;
    border: 0.0625em solid rgba(255,255,255,.2);
    width: 7.8125em;
    height: 7.8125em;
    border-radius: 100%
  }
  &__circle-outer {
    margin-left: 9.0625em;
    margin-top: -7.5em;
    border: 0.5em solid $mainColor;
    width: 8.625em;
    height: 8.625em;
    border-radius: 100%;
    border-left-color: transparent;
    border-top-color: transparent;
    transform: rotate(120deg);
    position: absolute;
    right: -3.75em;
    top: 3.625em;
    animation: spinloader 8s linear infinite;
    @include r(767) {
      animation: none;
    }
    @at-root {
      @keyframes spinloader {
        0% { transform: rotate(0) }
        50% { transform: rotate(180deg) }
        100% { transform: rotate(360deg) }
      }
    }
  }
}//b-video

.b-slider {
  &__nav {
    user-select: none;
  }
  &__prev, &__next {
    position: absolute;
    z-index: 5;
    top: 50%;
    margin-top: -32px;
    width: 64px;
    height: 64px;
    color: #707070;
    border: 5px solid #707070;
    transition: all 0.3s;
    &:hover {
      color: $mainColor;
      border-color: $mainColor;
      cursor: pointer;
    }
    & > .svg {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
  }
}//b-slider

.mfp-arrow-right, .mfp-arrow-left {
  opacity: 1;
  color: #000;
  transition: color 0.3s;
  &:active {
    margin-top: -55px;
  }
  &:hover {
    color: $mainColor;
  }
  &::after {
    content: none;
  }
  &::before {
    top: 50%;
    left: 50%;
    border: none;
    padding: 0;
    margin: 0;
    margin-top: -37px;
    margin-left: -37px;
    width: 74px;
    height: 74px;
    color: #000;
    background-color: #fff;
    border-radius: 50%;
    opacity: 1;
    transition: all 0.3s;
  }
  .svg {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
  }
}

.mfp-arrow-left {
  .svg {
    transform: translate(-55%, -50%);
  }
}

.mfp-arrow-right {
  .svg {
    transform: translate(-45%, -50%);
  }
}

.popup {
  &__title {
    margin-bottom: 20px;
    color: #000;
    font-size: 26px;
    text-align: center;
    @include r(479) {
      font-size: 22px;
    }
  }
  &-thanks {
    padding: 30px;
    max-width: 375px;
    margin: 0 auto;
    position: relative;
    text-align: center;
    background-color: #fff;
    img {
      display: block;
      margin: 0 auto 15px;
      width: 109px;
      height: 109px;
    }
    &__title {
      margin-bottom: 10px;
      color: #000;
      font-size: 30px;
      font-weight: 500;
    }
    &__text {
      color: #000;
      font-size: 15px;
      font-weight: 400;
      line-height: 22px;
    }
  }
}//popup
